<template>
  <Card>
    <template #header>
      <div class="flex items-center flex-wrap">
        <i class="material-icons mr-4">lock</i>
        <h6 class="text-lg font-semibold">Price Lock Settings</h6>
      </div>
    </template>
    <template #default>
      <Alert
        color="blue"
        view="smooth"
        class="mb-5"
        style="padding-top: 0; padding-bottom: 0; width: max-content"
        dense
      >
        <div class="flex items-center h-full">
          This is the amount of days your buyback offer is valid. Buybacks
          created that go past this many days will automatically move into
          expired offers.
        </div>
        <template #left>
          <span class="material-icons text-2xl">info</span>
        </template>
      </Alert>
      <Input
        v-maska="'##'"
        v-model="expiredDays"
        class="w-52"
        icon
        icon-block
        right
        :view="(validation.expired_days.$invalid && 'danger') || 'default'"
      >
        <template #icon>
          <span class="text-sm">Days</span>
        </template>
      </Input>
      <small
        class="text-red-base block"
        v-for="(error, errorIndex) of validation.expired_days.$errors"
        :key="errorIndex"
        >{{ error.$message }}. </small
      >
      <small class="block mt-4"
        ><span class="font-semibold">Max Days</span>: Up to 90 days</small
      >
    </template>
    <template #footer>
      <div class="flex justify-end">
        <Button
          color="teal"
          view="smooth"
          @click="handleUpdatePriceLock"
          :loading="loading.update"
          >Save</Button
        >
      </div>
    </template>
  </Card>
</template>

<script>
import { computed, defineComponent, reactive } from 'vue'
import Card from 'bbUi/components/card/card.vue'
import Alert from 'bbUi/components/alert/alert.vue'
import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'
import { useVuelidate } from '@vuelidate/core'
import { minValue, maxValue } from '@vuelidate/validators'

export default defineComponent({
  components: {
    Card,
    Alert,
    Input,
    Button
  },
  props: {
    modelValue: {
      type: [String, Number],
      default: 0
    },
    loading: {
      type: Object,
      default: () => ({
        all: false,
        update: false
      })
    }
  },
  emits: ['update:modelValue', 'update'],
  setup(props, { emit }) {
    const expiredDays = computed({
      set: (newVal) => {
        if (newVal > 90) {
          expiredDays.value = 90
          return null
        }
        emit('update:modelValue', newVal)
      },
      get: () => props.modelValue
    })
    const data = reactive({
      expired_days: expiredDays
    })
    const rules = {
      expired_days: {
        minValue: minValue(0),
        maxValue: maxValue(90)
      }
    }
    const validation = useVuelidate(rules, data, {
      $lazy: true
    })
    const validate = () => {
      validation.value.$validate()
      return !validation.value.$invalid
    }
    const handleUpdatePriceLock = () => {
      const isValid = validate()
      if (isValid) {
        emit('update', expiredDays.value)
      }
    }
    return {
      expiredDays,
      validation,
      validate,
      handleUpdatePriceLock
    }
  }
})
</script>

