<template>
  <div>
    <div class="flex xl:flex-row flex-col justify-between mb-8">
      <Breadcrumb
        pageTitle="General Settings"
        :backTo="{ name: 'setting' }"
        :items="breadcrumb"
      />
    </div>
    <Card class="mb-6" :style="{ overflow: 'unset' }">
      <template #header>
        <div class="flex items-center">
          <i class="material-icons mr-4">language</i>
          <h6 class="text-lg font-semibold">Country Settings</h6>
        </div>
      </template>
      <div
        class="h-full inline-flex border border-grey-01 rounded-md text-center flex items-center justify-center relative"
        v-outside="handleClickOutsideCountry"
      >
        <div
          class="flex items-center justify-center cursor-pointer py-2 pl-4 pr-2 w-60"
          @click="dropdownCountry.is_visibled = !dropdownCountry.is_visibled"
        >
          <span class="inline-block mr-2" v-if="countryActive">
            <img
              :src="`/flag/${countryActive.country_code.toLowerCase()}.svg`"
              width="16"
            />
          </span>
          <span class="text-base">
            {{ !!countryActive ? countryActive.nicename : 'Choose Country' }}
          </span>
          <span class="inline-block ml-4">
            <span class="material-icons text-lg"> arrow_drop_down </span>
          </span>
        </div>
        <div
          class="bg-white absolute z-50 border rounded border-grey-01 shadow py-4 left-0 top-14"
          style="min-width: 144px"
          v-show="dropdownCountry.is_visibled"
          v-loading="dropdownCountry.is_loading"
        >
          <div class="px-4">
            <Input
              v-model="dropdownCountry.query"
              placeholder="Search Country"
              class="w-full overflow-ellipsis text-sm"
              icon
            />
          </div>
          <div class="mt-4 max-h-60 overflow-x-auto">
            <ul>
              <li
                v-for="(country, countryKey) in defaultListCountry"
                :key="countryKey"
                @click="handleCountryChange(country.id)"
                class="text-left p-2 hover:bg-grey-lighten cursor-pointer px-4"
              >
                <div class="-mx-2 px-4 text-sm">
                  <div class="flex items-center">
                    <div class="w-min" v-if="countryActive">
                      <span
                        class="material-icons text-sm text-teal-04"
                        v-if="
                          country.country_code === countryActive.country_code
                        "
                      >
                        check_circle
                      </span>
                    </div>
                    <div class="flex flex-row">
                      <img
                        class="mx-4"
                        :src="`/flag/${country.country_code.toLowerCase()}.svg`"
                        width="16"
                      />
                      <span>{{ country.nicename }}</span>
                    </div>
                  </div>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <template #footer>
        <div class="flex justify-end">
          <Button
            view="smooth"
            color="teal"
            @click="saveActionCountry"
            :loading="loadingCountry.update"
            >Save</Button
          >
        </div>
      </template>
    </Card>
    <Card
      v-loading="loading.update || loading.all"
      class="mb-6"
      :style="{ overflow: 'unset' }"
    >
      <template #header>
        <div class="flex items-center">
          <i class="material-icons mr-4">local_atm</i>
          <h6 class="text-lg font-semibold">Currency Settings</h6>
        </div>
      </template>
      <div class="overflow-visible">
        <div class="inline-block">
          <Alert
            color="orange"
            view="smooth"
            class="mb-5"
            style="padding-top: 0; padding-bottom: 0"
            dense
          >
            <div class="flex items-center h-full">
              <span class="font-bold">Warning</span>: Price sync is currently
              only available for USD
            </div>
            <template #left>
              <span class="material-icons text-2xl">info</span>
            </template>
          </Alert>
        </div>
        <div>
          <h6 class="text-sm font-semibold mb-1">Currency</h6>
          <div
            class="h-full inline-flex border border-grey-01 rounded-md text-center flex items-center justify-center relative"
            v-outside="handleClickOutside"
          >
            <div
              class="flex items-center justify-center cursor-pointer py-2 pl-4 pr-2 w-40"
              @click="dropdown.is_visibled = !dropdown.is_visibled"
            >
              <span class="text-base">
                {{ !!currencyActive ? currencyActive : 'Choose Currency' }}
              </span>
              <span class="inline-block ml-2">
                <img
                  :src="`/flag/${currencyFlagActive.toLowerCase()}.svg`"
                  width="16"
                />
              </span>
              <span class="inline-block ml-4">
                <span class="material-icons text-lg"> arrow_drop_down </span>
              </span>
            </div>
            <div
              class="bg-white absolute z-50 border rounded border-grey-01 shadow py-4 left-0 top-14"
              style="min-width: 144px"
              v-show="dropdown.is_visibled"
              v-loading="dropdown.is_loading"
            >
              <div class="px-4">
                <Input
                  v-model="dropdown.query"
                  placeholder="Search Currency"
                  class="w-full overflow-ellipsis text-sm"
                  icon
                />
              </div>
              <div class="mt-4 max-h-60 overflow-x-auto">
                <ul>
                  <li
                    v-for="(defaultCurrency, key) in defaultListCurrency"
                    :key="key"
                    class="text-left p-2 hover:bg-grey-lighten cursor-pointer px-4"
                    :class="{
                      'bg-grey-lighten':
                        currencyActive === defaultCurrency.currency_code
                    }"
                    @click="handleCommand(defaultCurrency.id)"
                  >
                    <div class="flex justify-start items-center -mx-2 text-sm">
                      <div class="flex flex-row items-center px-2">
                        <div
                          v-if="
                            defaultCurrency.currency_code === currencyActive
                          "
                          class="flex-1 mr-4"
                        >
                          <span class="material-icons text-sm text-teal-04">
                            check_circle
                          </span>
                        </div>
                        <div class="flex-1">
                          {{ defaultCurrency.currency_code }}
                        </div>
                      </div>
                    </div>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
      <template #footer>
        <div class="flex justify-end">
          <Button view="smooth" color="teal" @click="saveActionCurrency"
            >Save</Button
          >
        </div>
      </template>
    </Card>
    <Card class="mb-6" :style="{ overflow: 'unset' }">
      <template #header>
        <div class="flex items-center">
          <i class="material-icons mr-4">watch_later</i>
          <h6 class="text-lg font-semibold">Time Zone</h6>
        </div>
      </template>
      <template #default>
        <div
          class="h-full inline-flex border border-grey-01 rounded-md text-center flex items-center justify-center relative"
          v-outside="handleClickOutsideTimezone"
        >
          <div
            class="flex items-center justify-center cursor-pointer py-2 pl-4 pr-2 w-69"
            @click="
              dropdownTimezone.is_visibled = !dropdownTimezone.is_visibled
            "
          >
            <span class="text-base">
              {{
                !!timezoneActive
                  ? timezoneActive.timezone_text
                  : 'Choose Timezone'
              }}
            </span>
            <span class="inline-block ml-4">
              <span class="material-icons text-lg"> arrow_drop_down </span>
            </span>
          </div>
          <div
            class="bg-white absolute z-50 border rounded border-grey-01 shadow py-4 left-0 top-14"
            style="min-width: 144px"
            v-show="dropdownTimezone.is_visibled"
            v-loading="dropdownTimezone.is_loading"
          >
            <div class="px-4">
              <Input
                v-model="dropdownTimezone.query"
                placeholder="Search Timezone"
                class="w-full overflow-ellipsis text-sm"
                icon
              />
            </div>
            <div class="mt-4 max-h-60 overflow-x-auto">
              <ul>
                <li
                  v-for="(timezone, timezoneKey) in defaultListTimezone"
                  :key="timezoneKey"
                  @click="handleTimezoneChange(timezone.timezone)"
                  class="text-left p-2 hover:bg-grey-lighten cursor-pointer px-4"
                >
                  <div class="-mx-2 px-4 text-sm">
                    <div class="flex items-center">
                      <div class="w-min mr-2" v-if="timezoneActive">
                        <span
                          class="material-icons text-sm text-teal-04"
                          v-if="timezone.timezone === timezoneActive.timezone"
                        >
                          check_circle
                        </span>
                      </div>
                      <div class="flex flex-row">
                        <span>{{ timezone.timezone_text }}</span>
                      </div>
                    </div>
                  </div>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </template>
      <template #footer>
        <div class="flex justify-end">
          <Button
            view="smooth"
            color="teal"
            @click="updateTimezone"
            :loading="loadingTimezone.update"
            >Save</Button
          >
        </div>
      </template>
    </Card>
    <!-- <PriceLock v-model="data.exp_days_pricelock" :loading="loadingPriceLock" @update="handleUpdatePriceLock" class="mb-6"></PriceLock> -->
    <!-- <BuybackPrefix :loading="loadingBuybackPrefix" :data="data.buyback_number" @update="handleUpdateBuybackPrefix"></BuybackPrefix> -->
  </div>
</template>
<script>
import { defineComponent, ref, computed, reactive } from 'vue'
import { useStore } from 'vuex'
import { useGeneralSetting } from 'composable/setting'
import { useUser } from 'composable/user'
import { ElMessage } from 'element-plus'

import Card from 'bbUi/components/card/card.vue'
import Breadcrumb from 'components/Breadcrumb.vue'
import Button from 'bbUi/components/button/button.vue'
import Alert from 'bbUi/components/alert/alert.vue'
import Autocomplete from 'bbUi/components/auto-complete/auto-complete.vue'
import Input from 'bbUi/components/input/input.vue'

import PriceLock from './price-lock/index.vue'
import BuybackPrefix from './buyback-prefix/index.vue'

export default defineComponent({
  components: {
    Card,
    Breadcrumb,
    Button,
    Alert,
    Input,
    Autocomplete,
    PriceLock,
    BuybackPrefix
  },
  async setup() {
    const store = useStore()
    const {
      actionShow,
      actionShowCountries,
      actionShowTimezones,
      actionShowPriceLock,
      actionShowBuybackPrefix,
      data,
      actionUpdate,
      actionUpdateCountry,
      actionUpdateTimezone,
      actionUpdatePriceLock,
      loading,
      loadingCountry,
      loadingTimezone,
      loadingPriceLock,
      loadingBuybackPrefix,
      stateUpdate
    } = useGeneralSetting()
    const { me } = useUser()
    const breadcrumb = [
      {
        text: 'Settings',
        to: {
          name: 'setting'
        }
      },
      {
        text: 'General Settings'
      }
    ]

    await Promise.all([
      actionShow(),
      actionShowCountries(),
      actionShowTimezones(),
      actionShowPriceLock(),
      actionShowBuybackPrefix()
    ])

    const currencies = data.currencies
    const countries = data.countries
    const timezones = data.timezones

    const currencyActive = ref(store.state.user.me.currency)
    const countryActive = ref(
      countries.find(
        (country) => country.country_code === me.value.country_code
      ) || null
    )
    const timezoneActive = ref(
      timezones.find((timezone) => timezone.timezone === me.value.timezone) ||
        null
    )

    const handleCommand = async (n) => {
      const source = [...currencies].find((x) => x.id === n)
      currencyActive.value = source.currency_code
      dropdown.is_visibled = false
    }

    const handleCountryChange = (countryId) => {
      const selectedCountry = [...countries].find(
        (country) => country.id === countryId
      )
      countryActive.value = selectedCountry
      dropdownCountry.is_visibled = false
    }

    const handleTimezoneChange = (timezone) => {
      const selectedTimezone = [...timezones].find(
        (eachTimezone) => eachTimezone.timezone === timezone
      )
      timezoneActive.value = selectedTimezone
      dropdownTimezone.is_visibled = false
    }

    const saveActionCurrency = async () => {
      const source = [...currencies].find(
        (x) => x.currency_code === currencyActive.value
      )
      if (!source) return
      await actionUpdate({
        currency: source.currency_code
      })

      if (stateUpdate.value == 'saved') {
        ElMessage({
          message: 'General settings updated!.',
          type: 'success'
        })
      }
    }

    const saveActionCountry = async () => {
      const isExistActiveCountry = !!countryActive.value
      if (isExistActiveCountry) {
        await actionUpdateCountry(
          {
            country_code: countryActive.value.country_code
          },
          () => {
            ElMessage({
              message: 'General settings updated!.',
              type: 'success'
            })
          }
        )
      }
    }

    const dropdown = reactive({
      is_visibled: false,
      is_loading: false,
      query: null
    })

    const dropdownCountry = reactive({
      is_visibled: false,
      is_loading: false,
      query: null
    })

    const dropdownTimezone = reactive({
      is_visibled: false,
      is_loading: false,
      query: null
    })

    const defaultListCurrency = computed(() => {
      const source = [...currencies].find(
        (x) => x.currency_code === currencyActive.value
      )
      const filtered = [...currencies].filter((x) => {
        if (!!dropdown.query) {
          return x.currency_code
            .toLowerCase()
            .includes(dropdown.query.toLowerCase())
        } else {
          if (
            x.currency_code === 'USD' ||
            x.currency_code === 'GBP' ||
            x.currency_code === 'AUD' ||
            x.currency_code === 'EUR'
          )
            return x
        }
      })
      if (![...filtered].find((x) => x.id === source.id))
        return [...[source], ...filtered]
      return [...filtered]
    })

    const defaultListCountry = computed(() => {
      const isExistActiveCountry = !!countryActive.value
      let selected = isExistActiveCountry
        ? [...countries].find(
            (country) =>
              country.country_code === countryActive.value.country_code
          ) || null
        : null
      const filteredCountries = [...countries].filter((country) => {
        const isSelectedCountry = selected ? selected.id === country.id : false
        if (!isSelectedCountry) {
          if (!!dropdownCountry.query)
            return (
              country.nicename
                .toLowerCase()
                .indexOf(dropdownCountry.query.toLowerCase()) > -1
            )
          return true
        }
        return false
      })
      if (selected) filteredCountries.unshift(selected)
      return filteredCountries
    })

    const defaultListTimezone = computed(() => {
      const isExistActiveTimezone = !!timezoneActive.value
      let selected = isExistActiveTimezone
        ? [...timezones].find(
            (timezone) => timezone.timezone === timezoneActive.value.timezone
          ) || null
        : null
      const filteredTimezones = [...timezones].filter((timezone) => {
        const isSelectedTimezone = selected
          ? selected.timezone === timezone.timezone
          : false
        if (!isSelectedTimezone) {
          if (!!dropdownTimezone.query)
            return (
              timezone.timezone_text
                .toLowerCase()
                .indexOf(dropdownTimezone.query.toLowerCase()) > -1
            )
          return true
        }
        return false
      })
      if (selected) filteredTimezones.unshift(selected)
      return filteredTimezones
    })

    const handleClickOutside = () => {
      dropdown.is_visibled = false
    }
    const handleClickOutsideCountry = () => {
      dropdownCountry.is_visibled = false
    }
    const handleClickOutsideTimezone = () => {
      dropdownTimezone.is_visibled = false
    }
    const handleUpdatePriceLock = async () => {
      await actionUpdatePriceLock(() => {
        ElMessage({
          type: 'success',
          message: 'General setting updated'
        })
      })
    }
    const handleUpdateBuybackPrefix = async(data) => {
      data.zero_padding_order_number_sequence = parseInt(String(data.zero_padding_order_number_sequence))
      await actionUpdate(data, async () => {
        ElMessage({
          type: 'success',
          message: 'Buyback number prefix updated'
        })
        await actionShowBuybackPrefix()
      })
    } 

    const updateTimezone = async () => {
      const isSelectedTimezone = !!timezoneActive.value
      if (isSelectedTimezone) {
        const payload = {
          timezone: timezoneActive.value.timezone
        }
        await actionUpdateTimezone(payload, () => {
          ElMessage({
            type: 'success',
            message: 'Timezone updated'
          })
        })
      }
    }

    const currencyFlagActive = computed(() => {
      const source = [...currencies].find((x) => {
        if (x.currency_code === currencyActive.value) return x.country_code
      })
      if (!!source) return source.country_code
      return null
    })

    return {
      data,
      timezones,
      breadcrumb,
      currencyActive,
      countryActive,
      timezoneActive,
      currencies,
      countries,
      handleCommand,
      saveActionCurrency,
      saveActionCountry,
      loading,
      loadingCountry,
      loadingTimezone,
      loadingPriceLock,
      loadingBuybackPrefix,
      defaultListCurrency,
      defaultListCountry,
      defaultListTimezone,
      dropdown,
      dropdownCountry,
      dropdownTimezone,
      handleClickOutside,
      handleClickOutsideCountry,
      handleClickOutsideTimezone,
      handleCountryChange,
      handleTimezoneChange,
      handleUpdatePriceLock,
      handleUpdateBuybackPrefix,
      updateTimezone,
      currencyFlagActive
    }
  }
})
</script>
