<template>
  <Card>
    <template #header>
      <div class="flex items-center flex-wrap">
        <i class="material-icons mr-4">shopping_basket</i>
        <h6 class="text-lg font-semibold">Buyback Number Settings</h6>
      </div>
    </template>
    <template #default>
      <div class="grid grid-cols-1 gap-6">
        <div>
          <Input
            v-maska="'S*'"
            v-model="data.prefix_order_number_sequence"
            label="Buyback Prefix"
            class="w-52"
            :view="
              (validation.prefix_order_number_sequence.$invalid && 'danger') ||
              'default'
            "
          ></Input>
          <small
            class="text-red-base block"
            v-for="(error, errorIndex) of validation
              .prefix_order_number_sequence.$errors"
            :key="errorIndex"
            >{{ error.$message }}. </small
          >
        </div>
        <!-- <div>
          <Input
            v-maska="'#*'"
            v-model="data.zero_padding_order_number_sequence"
            label="Buyback Zero Padding"
            class="w-52"
            :view="
              (validation.zero_padding_order_number_sequence.$invalid &&
                'danger') ||
              'default'
            "
          ></Input>
          <small
            class="text-red-base block"
            v-for="(error, errorIndex) of validation
              .zero_padding_order_number_sequence.$errors"
            :key="errorIndex"
            >{{ error.$message }}. </small
          >
        </div> -->
        <div>
          <Input
            v-model="data.preview"
            label="Preview"
            class="w-52"
            disabled
            :loading="loading.all"
          ></Input>
        </div>
      </div>
    </template>
    <template #footer>
      <div class="flex justify-end">
        <Button
          color="teal"
          view="smooth"
          @click="handleUpdateBuybackPrefix"
          :loading="loading.update"
          >Save</Button
        >
      </div>
    </template>
  </Card>
</template>

<script>
import { defineComponent, reactive, watch } from 'vue'
import Card from 'bbUi/components/card/card.vue'
import Alert from 'bbUi/components/alert/alert.vue'
import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'
import { useVuelidate } from '@vuelidate/core'
import { required } from '@vuelidate/validators'

export default defineComponent({
  components: {
    Card,
    Alert,
    Input,
    Button
  },
  props: {
    data: {
      type: Object,
      default: () => ({
        prefix_order_number_sequence: 'AS',
        // zero_padding_order_number_sequence: 5,
        preview: 'AS00001'
      })
    },
    loading: {
      type: Object,
      default: () => ({
        all: false,
        update: false
      })
    }
  },
  emits: ['update'],
  setup(props, { emit }) {
    const data = reactive({
      prefix_order_number_sequence:
        props.data.prefix_order_number_sequence || 'AS',
      zero_padding_order_number_sequence:
        props.data.zero_padding_order_number_sequence || 5,
      preview: props.data?.preview || 'AS00001'
    })
    const rules = {
      prefix_order_number_sequence: {
        required
      }
      // zero_padding_order_number_sequence: {
      //   required
      // }
    }
    const validation = useVuelidate(rules, data, {
      $lazy: true
    })
    const validate = () => {
      validation.value.$validate()
      return !validation.value.$invalid
    }
    const handleUpdateBuybackPrefix = () => {
      const isValid = validate()
      if (isValid) {
        emit('update', data)
      }
    }
    watch(
      () => props.data,
      (newData) => {
        Object.assign(data, {
          prefix_order_number_sequence: newData.prefix_order_number_sequence,
          // zero_padding_order_number_sequence:
          //   newData.zero_padding_order_number_sequence,
          preview: newData.preview
        })
      },
      {
        deep: true
      }
    )
    return {
      data,
      validation,
      validate,
      handleUpdateBuybackPrefix
    }
  }
})
</script>

